.inputSearch {
   width: 400px;
   height: 45px;

   position: relative;
}

.inputSearch input {
   width: 100%;
   height: 100%;
   border-radius: 50px;
   background: #eceff3;
   outline: none;
   border: none;
   padding: 8px 20px 8px 52px;
}

.inputSearch__Icon {
   position: absolute;
   left: 0;
   transform: translate(50%, 50%);
}
