@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,700;1,400&display=swap);
* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   font-family: "Poppins", sans-serif;
}

hr {
   border: 0.5px solid #c678dd;
}

/* Rules for sizing the icon. */
.md-18 {
   font-size: 18px;
}
.md-24 {
   font-size: 24px;
}
.md-36 {
   font-size: 36px;
}
.md-48 {
   font-size: 48px;
}

/* Rules for using icons as black on a light background. */
.md-dark {
   color: rgba(40, 44, 52, 0.54);
}
.md-dark-inactive {
   color: rgba(40, 44, 52, 0.26);
}

/* Rules for using icons as white on a dark background. */
.md-light {
   color: rgba(236, 239, 243, 1);
}
.md-light-inactive {
   color: rgba(236, 239, 243, 0.3);
}

/* Btn Styles */

.Btn {
   min-width: 140px;
   height: 48px;
   padding: 8px 24px;
   background: #282c34;
   border-radius: 30px;
   outline: none;
   border: none;

   font-style: normal;
   font-weight: normal;
   font-size: 16px;
   line-height: 135%;

   color: #eceff3;
}

.Btn:hover {
   -webkit-filter: brightness(140%);
           filter: brightness(140%);
   box-shadow: 0px 4px 4px rgba(40, 44, 52, 0.25);
}

.Btn--Box,
.Btn--Icon {
   position: relative;
}

.Btn--Box .Btn {
   min-width: 48px;
   border-radius: 12px;
}

.Btn--Icon--left .Btn {
   padding-left: 40px;
   padding-right: 16px;
}

.Btn--Icon--left .Btn__Icon {
   height: 15px;
   -webkit-transform: translate(110%, 110%);
           transform: translate(110%, 110%);
}

.Btn--Icon--right .Btn {
   padding-right: 40px;
}

.Btn--Icon--right .Btn__Icon {
   height: 15px;
   left: auto;
   right: 0;
   -webkit-transform: translate(-110%, 110%);
           transform: translate(-110%, 110%);
}

.Btn__Icon {
   position: absolute;
   left: 0;
   top: 0;
   -webkit-transform: translate(50%, 50%);
           transform: translate(50%, 50%);
}

.clickable {
   cursor: pointer;
}

/* forms style */

.Form {
   width: 800px;
   padding: 80px;
   display: flex;
   flex-direction: column;
   align-items: center;
   border: 1px solid #282c34;
   border-radius: 16px;
   color: #282c34;
}

.Form__Container {
   display: flex;
   align-items: center;
   justify-content: center;
   padding: 80px;
}

.Form h2 {
   margin-bottom: 20px;
}

.Form__Status {
   width: 100%;
   display: flex;
   flex-direction: column;
   align-items: flex-start;
}

.Form__Status p {
   font-style: normal;
   font-weight: normal;
   font-size: 14px;
   line-height: 17px;
}

.Form__Status__inputContainer {
   width: 100%;
   display: flex;
   justify-content: space-around;
}

.input {
   width: 100%;
   display: flex;
   flex-direction: column;
   font-style: normal;
   font-weight: normal;
   font-size: 14px;
   line-height: 17px;

   color: #282c34;
   margin-bottom: 32px;
}

.input input,
.input textarea {
   font-style: normal;
   font-weight: normal;
   font-size: 16px;
   line-height: 135%;
   margin-top: 12px;

   color: #282c34;
   padding: 12px;

   border: 1px solid #282c34;
   border-radius: 10px;
   outline: none;
}

.input input:focus-visible,
.input textarea:focus-visible {
   color: #282c34;
   border: 2px solid #c678dd;
}

.input--radio {
   display: flex;
   align-items: center;
}

.input--radio input {
   margin-right: 8px;
   width: 16px;
   height: 16px;
}

.autocomplete {
   border-radius: 10px;
}

.Main {
   width: 100%;
   display: flex;
   padding: 120px 80px 80px 80px;
}

.Main__Content {
   width: 60%;
   padding-right: 80px;
}

.Main__News {
   width: 40%;
   padding-left: 80px;
}

.gallery {
   width: 100%;
   margin-bottom: 80px;
   overflow: scroll;
}

.gallery--withoutPadding {
   margin-bottom: 0px;
}

.gallery::-webkit-scrollbar {
   display: none;
}

.gallery__row {
   display: flex;
   width: -webkit-fit-content;
   width: -moz-fit-content;
   width: fit-content;
}

.AnimeCard {
   width: 248px;
   height: 351px;
   border-radius: 15px;
   position: relative;
   margin-right: 40px;
}

.AnimeCard:hover {
   cursor: pointer;
}

.AnimeCard__img {
   width: 100%;
   height: 100%;
   object-fit: cover;
   border-radius: 15px;
}

.AnimeCard__figcaption {
   width: 100%;
   height: 60px;
   padding: 8px;
   background: rgba(14, 14, 44, 0.7);
   border-radius: 0px 0px 15px 15px;

   position: absolute;
   bottom: 0px;

   display: flex;
   align-items: center;
   justify-content: center;

   color: #eceff3;
}

.AnimeCard__figcaption p {
   width: 100%;

   font-style: normal;
   font-weight: bold;
   font-size: 16px;
   line-height: 135%;

   text-align: center;
}

.EpisodeCard {
   width: 536px;
   height: 303px;
   border-radius: 15px;
   position: relative;
   margin-right: 40px;
}

.EpisodeCard:hover {
   cursor: pointer;
}

.EpisodeCard__img {
   width: 100%;
   height: 100%;
   object-fit: cover;
   border-radius: 15px;
}

.EpisodeCard__figcaption {
   width: 100%;
   height: 80px;
   padding: 8px;
   padding-left: 32px;
   background: rgba(14, 14, 44, 0.7);
   border-radius: 0px 0px 15px 15px;

   position: absolute;
   bottom: 0px;

   display: flex;
   flex-direction: column;
   align-items: flex-start;
   justify-content: center;

   color: #eceff3;
}

.EpisodeCard__figcaption h1 {
   font-style: normal;
   font-weight: bold;
   font-size: 16px;
   line-height: 135%;
}

.EpisodeCard__figcaption h2 {
   font-style: normal;
   font-weight: lighter;
   font-size: 16px;
   line-height: 135%;
}

.Title {
    display: flex;
    align-items: center;

    margin-bottom: 40px;
}

.Title h2 {
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 135%;

    color: #282c34;
    padding-right: 20px;
}

.Title a {
    text-decoration: none;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 17px;

    color: #c678dd;
}

.header {
   width: 100%;
   height: 120px;
   padding: 0px 60px;

   background: linear-gradient(113.3deg, #535a68 0%, #3c414c 42.54%, #272b33 100%);
   opacity: 0.95;

   display: flex;
   justify-content: space-between;
   align-items: center;
}

.header__section {
   display: flex;
   align-items: center;
}

.header__logo {
   width: 202px;
   margin-right: 80px;
}

.Link {
    margin-right: 40px;

    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 17px;

    color: #eceff3;
    text-decoration: none;
}

.Link--active {
    color: #c678dd;
    text-decoration: none;
}

.inputSearch {
   width: 400px;
   height: 45px;

   position: relative;
}

.inputSearch input {
   width: 100%;
   height: 100%;
   border-radius: 50px;
   background: #eceff3;
   outline: none;
   border: none;
   padding: 8px 20px 8px 52px;
}

.inputSearch__Icon {
   position: absolute;
   left: 0;
   -webkit-transform: translate(50%, 50%);
           transform: translate(50%, 50%);
}

.Banner {
   width: 100%;
   height: 600px;
   padding-left: 120px;
   padding-bottom: 40px;
   position: relative;

   display: flex;
   align-items: flex-end;
   background-size: cover;
   background-position: center center;
}

.Banner__Name {
   max-width: 480px;
   padding-bottom: 40px;
   color: #ffffff;
   font-family: "Poppins", sans-serif;
   font-style: normal;
   font-weight: bold;
   font-size: 48px;
   line-height: 135%;
}

.Banner__Btns {
   position: absolute;
   left: 50%;
   width: 80px;
   display: flex;
   justify-content: space-around;
}

.Banner__BtnNav {
   background-color: #eceff3;
   border-radius: 16px;
   width: 16px;
   height: 16px;
}
.Banner__BtnNav--selected {
   background-color: #c678dd;
}

.Banner__BtnNav:hover {
   cursor: pointer;
}

.AnimeView {
   padding: 80px;
   color: #282c34;
}

.AnimeView__Info {
   display: flex;
   margin-bottom: 80px;
}

.AnimeView__Image {
   width: 248px;
   height: 351px;
   border-radius: 15px;
   margin-right: 40px;
}

.AnimeView__Name {
   margin-bottom: 16px;
   font-style: normal;
   font-weight: bold;
