   margin-right: 8px;
}

.AnimeView__Status {
   color: #c678dd;

   font-style: normal;
   font-weight: bold;
   font-size: 18px;
}

.AnimeView__Functions {
   display: flex;
   grid-gap: 20px;
   gap: 20px;
}

.EpisodeView {
   width: 100%;
   height: 100%;
   padding: 40px;
   display: flex;
}

.EpisodeView__Content {
   width: 70%;
   padding-right: 80px;
}

.EpisodeView__Info {
   display: flex;
   align-items: center;
   margin-bottom: 62px;
}

.EpisodeView__Info img {
   object-fit: cover;
   width: 280px;
   height: 158px;
   border-radius: 15px;
   position: relative;
   margin-right: 40px;
}

.EpisodeView__Data {
   max-width: 700px;
}

.EpisodeView__Anime {
   margin-bottom: 16px;
   font-weight: bold;
