}

.StudioView__Name {
   margin-bottom: 16px;
   font-style: normal;
   font-weight: bold;
   font-size: 36px;
}

.StudioView__Description {
   margin-bottom: 8px;
}

.StudioView__Date {
   margin-bottom: 32px;
}

.StudioView__News {
   width: 40%;
   padding-left: 80px;
}

.StudioView__Chart {
   margin-bottom: 24px;
}

.News {
   display: flex;
   align-items: center;
   width: 100%;
   margin-bottom: 60px;
}

.News p {
   font-size: 12px;
}

.News__Img {
   width: 120px;
   height: 120px;
   border-radius: 16px;
   margin-right: 16px;
}

.News__Info {
   width: 100%;
}

.News__Title {
   font-size: 14px !important;
